export default {
	data() {
		return {
			userInfo:null
		}
	},
	methods: {
		
		changeAccount()
		{
			uni.showToast({
				title: "暂未开发"
			})
		},
		logout()
		{
			uni.request({
				url: this.serverUrl + '/logout',
				method: 'GET',
				data: {},
				success: res => {
					uni.navigateBack({
						delta: 1
					})
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('userInfo')
	}
}
